import useGlobalStore from '@/store/common/global'
import { TransactionMode } from '@/types'
import { getPercentage } from '@/utils'
import { Block, CetusTooltip, SlippageSetting } from '@cetus/design'
import { PoolType, SlippageType } from '@cetus/design/src/components/common/SlippageSetting'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble, d } from '@cetus/utils'
import { HStack, Text } from '@chakra-ui/react'
import { useMemo, useState } from 'react'

type SlippageProps = {
  slippageType?: SlippageType
  poolType?: PoolType
  toolTipText?: string
  compact?: boolean
  isWidget?: boolean
  maxSlippage?: number
  showFastMode?: boolean
  onClick?: () => void
}

function Slippage(props: SlippageProps) {
  const { onClick, toolTipText, maxSlippage, poolType, showFastMode } = props
  const {
    onSlippageVisible,
    showSlippage,
    isWidget,
    compact,
    settingOpen,
    onClose,
    onChange,
    slippageType,
    transactionMode,
    handleChangeTransactionMode,
    maxCapForGas,
    handleChangeMaxCapForGas,
    showMevProtect,
    handleChangeMevProtect,
    customGasPrice,
    handleChangeCustomGasPrice
  } = useSlippage(props)
  return (
    <>
      <CetusTooltip tooltip={<Text fontSize="12px">{toolTipText || 'Settings'}</Text>}>
        {slippageType !== 'zap' ? (
          <HStack
            h="28px"
            p="4px 8px"
            borderRadius="8px"
            border="1px solid"
            borderColor="border"
            bg={isWidget ? 'swap_bg_secondary' : 'bg_secondary'}
            cursor="pointer"
            gap="6px"
            onClick={e => {
              cancelBubble(e)
              if (isWidget) {
                onClick && onClick()
              } else {
                onSlippageVisible()
              }
            }}
            sx={{
              _hover: {
                p: {
                  color: 'text_caption'
                },
                svg: {
                  fill: 'text_caption'
                }
              }
            }}
          >
            <>
              <Text fontWeight="500" fontSize="12px">
                {getPercentage(showSlippage)}
              </Text>
              <Block bg={isWidget ? 'swap_bg_secondary' : 'bg_secondary'} p="0" h="28px" w="28px" borderRadius="8px" mr="-9px">
                <HStack align="center" justify="center" mt="3px">
                  <Icon xlinkHref="#icon-icon_verticalslider" />
                </HStack>
              </Block>
            </>
          </HStack>
        ) : (
          <HStack
            gap="4px"
            borderBottom="1px dashed"
            borderColor="text_paragraph"
            pb="2px"
            onClick={e => {
              cancelBubble(e)
              if (isWidget) {
                onClick && onClick()
              } else {
                onSlippageVisible()
              }
            }}
          >
            <Text fontWeight="500" color="text_caption" fontSize="12px">
              {getPercentage(showSlippage)}
            </Text>
            <Text color="text_paragraph" fontSize="12px">
              Slippage
            </Text>
          </HStack>
        )}
      </CetusTooltip>
      {!isWidget && (
        <SlippageSetting
          showFastMode={showFastMode}
          maxSlippage={maxSlippage}
          isWidget={isWidget}
          compact={compact}
          isOpen={settingOpen}
          onClose={onClose}
          value={d(showSlippage).mul(100).toNumber()}
          onChange={onChange}
          slippageType={slippageType}
          poolType={poolType}
          transactionMode={transactionMode}
          onChangeTransactionMode={handleChangeTransactionMode}
          maxCapForGas={maxCapForGas}
          onChangeMaxCapForGas={handleChangeMaxCapForGas}
          mevProtect={showMevProtect}
          onChangeMevProtect={handleChangeMevProtect}
          customGasPrice={customGasPrice}
          onChangeCustomGasPrice={handleChangeCustomGasPrice}
        />
      )}
    </>
  )
}

export function useSlippage(props: SlippageProps) {
  const { slippageType = 'global', compact, poolType, isWidget = false, showFastMode = true } = props
  const {
    slippage,
    setSlippage,
    vaultsSlippage,
    setVaultsSlippage,
    liquiditySlippage,
    setLiquiditySlippage,
    // settingOpen,
    // setSettingOpen,
    mevProtect,
    dlmmMevProtect,
    setDlmmMevProtect,
    setMevProtect,
    setTransactionMode,
    setMaxCapForGas,
    transactionMode,
    maxCapForGas,
    customGasPrice,
    setCustomGasPrice,
    zapSlippage,
    setZapSlippage,
    deepBookSlippage,
    setDeepBookSlippage,
    crossSwapSlippage,
    setCrossSwapSlippage,
    mergeSwapSlippage,
    setMergeSwapSlippage
  } = useGlobalStore()
  const [settingOpen, setSettingOpen] = useState(false)
  const { isApp } = useWindowWidth()
  const onSlippageVisible = () => {
    setSettingOpen(!settingOpen)
  }

  const onClose = () => {
    setSettingOpen(false)
  }

  const onChange = (value: number) => {
    console.log('Slippage üöÄ ~ onChange ~ value:', value)
    console.log('Slippage üöÄ ~ onChange ~ slippageType:', slippageType)
    const saveSlippageValue = d(value).div(100).toNumber()
    if (slippageType === 'vaults') {
      setVaultsSlippage(saveSlippageValue)
    } else if (slippageType === 'zap') {
      console.log('Slippage üöÄ ~ onChange ~ saveSlippageValue:', saveSlippageValue)
      setZapSlippage(saveSlippageValue)
    } else if (slippageType === 'liquidity') {
      setLiquiditySlippage(saveSlippageValue)
    } else if (slippageType === 'deepbook') {
      setDeepBookSlippage(saveSlippageValue)
    } else if (slippageType === 'cross') {
      setCrossSwapSlippage(saveSlippageValue)
    } else if (slippageType === 'merge_swap') {
      setMergeSwapSlippage(saveSlippageValue)
    } else {
      setSlippage(saveSlippageValue)
    }
  }

  const slippageMap = {
    global: slippage,
    vaults: vaultsSlippage,
    zap: zapSlippage,
    liquidity: liquiditySlippage,
    cross: crossSwapSlippage,
    deepbook: deepBookSlippage,
    merge_swap: mergeSwapSlippage
  }

  const showSlippage = useMemo(() => {
    return slippageMap[slippageType]
  }, [slippageType, slippage, vaultsSlippage, zapSlippage, liquiditySlippage, crossSwapSlippage, deepBookSlippage, mergeSwapSlippage])

  const showMevProtect = useMemo(() => {
    return poolType === 'dlmm' || !showFastMode ? dlmmMevProtect : mevProtect
  }, [poolType, dlmmMevProtect, mevProtect])

  const handleChangeTransactionMode = (value: TransactionMode) => {
    setTransactionMode(value)
  }

  const handleChangeMaxCapForGas = (value: string) => {
    setMaxCapForGas(value)
  }

  const handleChangeCustomGasPrice = (value: string) => {
    setCustomGasPrice(value)
  }

  const handleChangeMevProtect = (value: boolean) => {
    // Â¶ÇÊûú‰∏çÊâìÂºÄfast modeÔºåÂàôÂ≠òÂÇ®Âú®ÂçïÁã¨ÁöÑDlmmMevProtect
    if (poolType === 'dlmm' || !showFastMode) {
      setDlmmMevProtect(value)
    } else {
      setMevProtect(value)
    }
  }

  return {
    onSlippageVisible,
    onClose,
    onChange,
    showSlippage,
    isWidget,
    compact,
    settingOpen,
    slippageType,
    transactionMode,
    handleChangeTransactionMode,
    maxCapForGas,
    handleChangeMaxCapForGas,
    showMevProtect,
    handleChangeMevProtect,
    customGasPrice,
    handleChangeCustomGasPrice
  }
}

export default Slippage
